html {
  box-sizing: border-box;
  font-size: 100%;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.upper-case {
  text-transform: uppercase;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, img, sub, sup, dl, dt, dd, ol, ul, li, form, label, table, caption, article, figure, figcaption, footer, header, section {
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

a {
  color: #fff;
  text-decoration: none;
}

